<template>
	
<div class="publish_page">
	

	
	<header-top title='信息发布' back></header-top>
	<div class="fixed-top-div"></div>




    <div class="list">
        <ul>
<!--            	<router-link to="/publish/createTender" tag='li'>发布现货交易</router-link>
           	<router-link to="/publish/" tag='li'>发布采购</router-link>
           	<router-link to="/publish/createSupply" tag='li'>发布供应</router-link>
 -->
           	<li @click="toPage('/publish/createTender')">发布现货交易</li>
           	<li @click="toPage2('/publish/createPurchase')">发布采购</li>
           	<li @click="toPage2('/publish/createSupply')">发布供应</li>


        </ul>
    </div>

	<transition>
		<router-view></router-view>
	</transition>


</div>

</template>
<script>
	
import {checkLogin,checkLogin2,checkAndToAuthen} from '@/tool/tool'

import headerTop from '@/components/header/header'

export default{
	components:{
		headerTop
	},
	methods:{
		//同时验证登录和实名认证
		toPage(url){
			if(!checkLogin(true)) return;
			if(!checkAndToAuthen(this)) return;
			this.$router.push(url)
		},
		//只检查登录
		toPage2(url){
			if(!checkLogin(true)) return;
			this.$router.push(url)
		}
	}





	}
</script>

<style lang='less' scoped>
@import '../../assets/css/variable.less';	

.publish_page{

	.fixed();
	/*background:#f5f5f5;*/
/*	width:100%;
	height:100%;*/
}

.list{
	li{
		line-height:1.8rem;
		border-bottom:1px solid #ccc;
		font-size:.8rem;
		padding:.3rem 1rem;
		color:#555;
		position: relative;


		&:after{

			content:'';
			width:.6rem;
			height:.6rem;
			border-top:2px solid #ccc;
			border-right:2px solid #ccc;
			right:1rem;
			top:0;
			bottom:0;
			margin:auto;
			position:absolute;
			transform:rotate(45deg);



		}
	}
}





</style>


