import {getCookie} from '@/config/util'


export const setCookie = (name,value) => {
    var Days = 1;
    var exp = new Date();
    //b保存cookie一段时间
   //exp.setTime(exp.getTime() + Days*24*60*60*1000);
    //exp.setTime(exp.getTime());
    //document.cookie = name + "="+ escape (value) + ";path=/;expires=" + exp.toGMTString();

    exp.setTime(exp.getTime() + Days*24*60*60*1000);
    exp.setTime(exp.getTime());
    //默认过期时间为本次回话结束
    document.cookie = name + "="+ escape (value);
}


//验证是否登录
export const checkLogin = (tips)=>{
	if(getCookie('vip')){
		return true
	}else{
		if(tips){
			layer.msg("请先登录！");
		}
		return false;
	}
}

//验证是否登录，并附带跳转链接
export const checkLogin2 = (obj)=>{
	if(getCookie('vip')){
		return true
	}else{
		layer.login(obj);
		return false;
	}
}



//验证实名认证状态
export const checkAuthen = (tips)=>{
	if(getCookie('personalAuthen')==1||getCookie('companyAuthen')==1){
		return true;
	}else{
		layer.msg("请先完成实名认证！")
		return false;
	}
}
//正确验证方法，并根据参数tips来决定是否显示提示信息。
export const checkAuthen2 = (tips)=>{
	if(getCookie('personAuthenState')==2 || getCookie('companyAuthenState')==2){
		return true;
	}else{
		if(tips){
			layer.msg("请先完成实名认证！")
		}
		return false;
	}
}

//检查实名认证并附带跳转链接
export const checkAndToAuthen = (obj)=>{
	if(getCookie('personAuthenState')==2 || getCookie('companyAuthenState')==2){
		return true;
	}else{
		layer.open({
			content:"请先完成实名认证！",
			// time:1.5,
			style:"width:75%",
			btn:['确定','取消'],
			yes:function(index){
				layer.close(index);
				obj.$router.push({path:'/user',query:{toAuthen:"toAuthen"}})
			}
		})
		return false;
	}
}


//判断会员状态
export const checkMember = (obj)=>{
	//usr_status = 2，表示是会员。
	if(getCookie("usr_status")==2){
		return true;
	}else{
		layer.msg("该功能需要会员才能查看！")
		return false;
	}
}

//判断登录和实名认证
export const checkLoginAndAuthen = ()=>{
	if(getCookie('personalAuthen')!=1||getCookie('companyAuthen')!=1){
		//检查实名认证
		return false;
	}
	if(!getCookie('vip')){
		//登录
		return false;
	}
	return true;
}





